import React, { Component } from "react";
import "./styles/output.css";
import "./App.css";
import FileInput from "./component/FileInput.js";

class App extends Component {
  constructor(props) {
    super(props);
    this.addTodo = this.addTodo.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.state = {
      todo: "",
      checked: 0,
      list: [],
      edit: '',
      fileInput: 0,
      newName: '',
      filter: 'all',

    };
  }


  


  isChecked(id) {
    console.log(id);
    const completedList = this.state.list.map(items => {

      if (id === items.id) {
        return { ...items, completed: !items.completed }
      }

      return items;
    })

    console.log(completedList);
    this.setState({ list: completedList })
  }

  // DELETE
  handleDelete(key) {
    const list = [...this.state.list]
    console.log(key);
    const updateList = list.filter(l => l.id !== key)
    console.log(updateList);
    this.setState({
      list: updateList
    })
  }


  setFilter(f) {
    console.log(f);
    this.setState({filter: f})
  }


  handleEdit = (status) => {
    this.setState({ edit: status })


  }

  handleChange(e) {
    this.setState({ newName: e.target.value })
  }



  // ADD
  addTodo = (e) => {
    this.setState({ todo: e.target.value });
    if (e.keyCode === 8) { // for backspace key
      console.log(e.target.value);
      console.log(e.target.value[e.target.selectionStart - 1]);
    } else if (e.keyCode === 46) { // for delete key
      console.log(e.target.value[e.target.selectionStart]);
    }
    if (e.keyCode === 191) {
      this.setState({ fileInput: 1 })
    }

    if (this.state.todo !== "") {

      // On Enter Add
      if (e.key === "Enter") {
        const todo = {
          id: Math.random(),
          value: this.state.todo,
          completed: false
        };
        const list = [...this.state.list];

        list.push(todo);
        console.log(list);
        this.setState({
          list,
          todo: "",
        });
      }
    }

  };

  onSave = (id) => {
    const editedList = this.state.list.map(items => {
      console.log(id);
      if (id === items.id) {
        return { ...items, value: this.state.newName }
      }
      return items;
    });
    console.log(editedList);

    this.setState({ list: editedList, edit: 0 })

  }




  render() {
    return (
      <div className="App  bg-todo  h-screen">
        <div className="container pt-52  flex justify-center items-center gap-5 flex-col">
          <div className="todo-container w-64 bg-todo flex flex-col bg-white shadow-md pr-1">
            <input
              className="  bg-white border-r-0 py-2 pl-2 "
              type="text"
              onKeyUp={this.addTodo}
            />
            <FileInput vi={this.state.fileInput} />
          </div>
          <div className="w-64">
            {this.state.list.map((items) => {

              return (<div className="todo-list grid mb-3   gap-3 ">

                {this.state.edit === items.id ?
                  <div className="flex flex-col gap-2">

                    <input type="text" onChange={this.handleChange} className="w-full border-r-0 py-1 pl-2 " />
                    <div className="flex gap-2 flex-grow">
                      <button onClick={() => this.onSave(items.id)} className="btn  bg-blue-500 text-white px-4 py-1 text-md">Save</button>
                      <button className="btn  bg-red-500 text-white px-4 py-1 text-md">Cancel</button>
                    </div>
                  </div>
                  :
                  <div>
                   
                    { this.state.filter === "all" ||  this.state.filter === items.completed ?
                      <div className="flex flex-col gap-2">
                        <label
                          className={items.completed ? 'flex items-center gap-2 w-64  line-through' : 'flex items-center gap-2 w-64 '}>
                          <input type="checkbox" id={items.id} checked={items.completed} name="todolist" onChange={() => this.isChecked(items.id)} />
                          {items.value}
                        </label>
                        <div className="flex gap-1">


                          <button onClick={() => this.handleEdit(items.id)} className="btn bg-blue-500 text-white px-4 py-1 text-md">Edit</button>
                          <button onClick={() => this.handleDelete(items.id)} className="btn bg-red-500 text-white px-4 py-1 text-md ml-1">Delete</button>
                        </div>
                      </div>
                  :
                   
                   ''
                }
                </div>
                }
               
              </div>)

            })}
          </div>
          <div>

            <button className="btn bg-blue-500 text-white px-4 py-1 text-md" onClick={() => this.setFilter('all')} >All</button>
            <button className="btn bg-blue-500 text-white px-4 py-1 text-md" onClick={() => this.setFilter(false)} >Active</button>
            <button className="btn bg-blue-500 text-white px-4 py-1 text-md" onClick={() => this.setFilter(true)} >Completed</button>


          </div>
        </div>
      </div>
    );
  }
}

export default App;
